import { useState } from "react";

const ProductSize = ({
  availableSize,
  isActive,
  index,
  setActiveIndex,
  handleSize,
}) => {
  return !isActive ? (
    <span
      className="catalog-item-size"
      onClick={() => {
        setActiveIndex(index);
        //      
        handleSize(availableSize.size);
      }}
    >
      {availableSize.size}
    </span>
  ) : (
    <span
      className="catalog-item-size selected"
      // onClick={() => setActiveIndex(null)}
    >
      {availableSize.size}
    </span>
  );
};

export default ProductSize;
